const menu = (buttons)=>{
 
    const menuLinks = document.querySelectorAll(buttons);

    if(menuLinks.length > 0){
        menuLinks.forEach(item => {
            item.addEventListener('click',MoveToBlock)
        });   
    }

    function MoveToBlock(e) {
        const target = e.target;
        if(target.dataset.goto && document.querySelector(target.dataset.goto)){//1.     2.       ( )
            const block = document.querySelector(target.dataset.goto);
            const BlockValue = block.getBoundingClientRect().top + window.pageYOffset - document.querySelector('header').offsetHeight; // 1.    2.  px 3. 
            console.log(BlockValue) //    

            window.scrollTo({//  
                top: BlockValue,// 
                behavior: "smooth"
            });
            e.preventDefault();
        }
    }


    


}
export default menu;